import React from 'react';
import "./document-viewer.css"

class DocumentViewer extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        const { documentData } = this.props;

        if (!documentData) {
          return null; // Return null if documentData is empty or undefined
        }
    
        const pdfBlob = new Blob([documentData], { type: 'application/pdf' });
        console.log(pdfBlob)
        const pdfUrl = URL.createObjectURL(pdfBlob);
        console.log(pdfUrl)
        return(
            <div className='document-viewer'>
                <iframe sandbox="allow-same-origin allow-scripts allow-popups allow-forms" src={pdfUrl} frameBorder="0" width="100%" height="100%"></iframe>
            </div>
        );
    }
}

export default DocumentViewer;