import React from 'react';
import "./tool-page.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button' 
import EndzoneGameSelect from '../../../components/selectors/endzone-game-select/endzone-game-select';

import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import SettingsIcon from '@mui/icons-material/Settings';


import logo from './images/Endzone_Shield.png'
import DocumentViewer from '../../misc-items/document-viewer/document-viewer';

import axios from 'axios'

class ToolPage extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            documentUrl: null
        }
    }

    runTool = async () => {
        this.runtimeLoading();
        await this.requestReport();
        this.runtimeComplete();
    }

    requestReport = async () => {
        await axios.get(
            this.props.requestUrl
        ).then (
            (response) => {
                console.log(response)
                this.setState({documentUrl: response.data});
            },
            (error) => {
                console.log("Error");
                console.log(error);
            }
    )}

    runtimeLoading =  () => {
        this.state = {documentUrl: "./"}
        document.getElementById("display-div").style.display = "none"
        document.getElementById("load-screen").style.display = "inline-flex" 
        document.getElementById("load-div").style.animation = "loading_spin 1.25s linear infinite";
        var load_text = document.getElementById("loading-text");
        load_text.style.opacity = "1"
    }

    runtimeComplete = () => {
        document.getElementById("load-div").style.animation = "";
        document.getElementById("load-screen").style.display = "none" 
        document.getElementById("display-div").style.display = "block"
        
    }

    render() {
        return(
            <div className='tool-page'>
                <EndzoneNav links = {this.props.links}></EndzoneNav>
                <div className='tool-input-div'>
                    <div className='tool-header'>
                        <h3>{this.props.reportName}</h3>
                    </div>
                    <div className='tool-body'>
                        <div className='tool-games'>
                            <EndzoneGameSelect></EndzoneGameSelect>
                        </div>
                        <div className='tool-container'>
                        <EndzoneButton text = "Configure Tool" icon = {<SettingsIcon sx = {{"font-size": '150%'}} ></SettingsIcon>}></EndzoneButton>
                        </div>
                        <div className='tool-container'>
                            <EndzoneButton onClick = {this.runTool} style={{"backgroundColor": "#054f88"}} text = "Run Tool" icon = {<PlayCircleOutlineIcon sx = {{"font-size": '150%'}}></PlayCircleOutlineIcon>}></EndzoneButton>
                        </div>
                    </div>
                </div>
                <div className='tool-output-div'>
                    <div className='load-screen' id = "load-screen">
                        <div className='load-div' id = "load-div">
                            <img src={logo} className = "tool-logo"></img>
                            <h3 id = "loading-text" className ='loading-text'>Loading...</h3>
                        </div>
                    </div>
                    <div id = "display-div" className='display-div'>
                        <DocumentViewer documentData = {this.state.documentUrl}></DocumentViewer>
                    </div>
                    <div id = "configure-div" className='configure-div'>                        
                    </div>
                </div>
            </div>
        );
    }
}



export default ToolPage;