import React from 'react';
import "./pre-game-report.css"

import ToolPage from '../../../components/tools/tool-page/tool-page';

class PreGameReport extends React.Component {
    constructor (props) {
        super(props);
    }
    render() {
        return(
            <div className='tool-page'>
                <ToolPage reportName = "Pre-Game Report" requestUrl = "/endzone/reports/test" links = {
                    [
                        {title: "Return to Hub", link: "Test"},
                        {title: "Pre-Game Tools", link: "Test"}
                    ]
                }>
                </ToolPage>
            </div>


        );
    }
}



export default PreGameReport;